@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap);
.html-text :first-child{
    font-size:1.22rem !important;
}
.html-text > p{
    margin: 0px;
    font-weight: bold;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 90%;  
}
.html-text > p > :first-child{
    background-color: transparent !important;
    color: inherit !important;
}
.html-text{
    max-height: 26px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 100%;  
    margin-bottom: 0.5rem;
}
.card{
    border: none;
}

.tag{
    margin-left: 7%;
    /* border: 1px solid; */
    max-height: 26px;
    cursor: pointer;
    border-color: #a07ee1 !important;
    color: #a07ee1;
    background: #dbcafa;
    font-weight: 700;
}

.Update{
    border-color: #5ed795 !important;
    color: #5ed795;
    background: #d1f5e1;
}

.Internship{
    border-color: #a07ee1 !important;
    color: #a07ee1;
    background: #dbcafa;
}

.Placement{
    border-color: rgb(235 58 101) !important;
    color: rgb(235 58 101);
    background: #fda7a7;
}

.Hackathon{
    border-color: #ffa360 !important;
    color: #ffa360;
    background: #f9dac4; 
}

/* webinar colors need to be changed */
.Webinar{
    border-color: #ffa360 !important;
    color: #ffa360;
    background: #f9dac4; 
}

.view_more{
    display: none;
    background: linear-gradient(to right, rgb(251, 123, 74), rgb(238, 51, 68));
    border-color: rgb(251, 123, 74);
}

.vertical-timeline::before{
    background: #f9f9f9;
}

.text-logo{
    font-family: 'Montserrat', sans-serif;
}

.vertical-timeline-element-date{
    display:none !important
}

.vertical-timeline-element-icon{
    top:20% !important
}

.vertical-timeline-element{
    margin:2em !important
}

@media only screen and (max-width: 600px) {
    .vertical-timeline-element-content{
        margin-left: 0px;
    }
    .vertical-timeline-element-icon{
        display: none !important;
    }
    .vertical-timeline::before{
        display: none !important;
    }
    .view_more{
        display: inline-block;
    }
}
.icon-Update{
width: 76% !important;
height: 76% !important;
transform: translate(-4%, 8%);
}
